
.menu-con {
  width: 35%;
  height: 100%;
  background: #333;
  z-index: 999;
  position: absolute;
  left: 0;
  top: 45px;
  -webkit-animation: moveWidth 0.5s forwards linear;
          animation: moveWidth 0.5s forwards linear;
}
.menu-con ul li {
  height: 60px;
  line-height: 60px;
  padding-left: 5px;
  border-bottom: thin solid #aaa;
  opacity: 0.6;
}
.menu-con ul li.active {
  opacity: 1;
}
.menu-con ul li h1 {
  font-size: 17px;
  font-family: '微软雅黑';
}
.menu-con ul li img {
  float: right;
  padding: 22px 5px 0 0;
  opacity: 0.7;
}
@-webkit-keyframes moveWidth {
0% {
    width: 35%;
}
35% {
    width: 45%;
}
75% {
    width: 60%;
}
100% {
    width: 75%;
}
}
@keyframes moveWidth {
0% {
    width: 35%;
}
35% {
    width: 45%;
}
75% {
    width: 60%;
}
100% {
    width: 75%;
}
}


