
#toolbar {
  width: 100%;
  height: 45px;
  background: #282828;
}
#toolbar img {
  width: 20px;
  height: 20px;
}
#toolbar h1 {
  float: left;
  color: #fff;
}
#toolbar h1 .toolbar-title-icon {
  width: 25px;
  height: 45px;
}
#toolbar h1 .toolbar-title-icon .menu-ing {
  display: inline-block;
  position: absolute;
  left: 5px;
  top: 12px;
  opacity: 0.8;
}
#toolbar h1 .toolbar-title-icon .menu-ing.active {
  opacity: 1;
}
#toolbar h1 .toolbar-title {
  display: inline-block;
  margin-left: 35px;
  line-height: 45px;
  font-size: 16px;
  font-family: "微软雅黑";
}
#toolbar #nav-right {
  width: 30px;
  height: 35px;
}
#toolbar #nav-right .user-img {
  position: absolute;
  left: 90%;
  top: 12px;
  opacity: 0.8;
}


